<script>
export default {
  name: 'MyHeadline',
  // å®ä¹å±æ§æ¥æ¶éç¨æ·çè¾å¥
  props: {
    level: {
      type: Number, // h1,h2,h3...
      default: 1,
      required: false
    },
    icon: {
      type: String, // å¾æ 
      default: '', // é»è®¤æ²¡æå¾æ 
      required: false
    },
    size: {
      type: String, // å¤§å°
      default: 'medium', // é»è®¤ä¸ºmedium
      required: false
    }
  },
  data () {
    return {

    }
  },
  render (h) {
    //  <h2 class="ui header">
