<template>
  <div class="slider" @click="hClick" @mouseleave="hMouseleave" @mouseenter="hMouseenter">
    <div class="slider-content">
      <!-- nameä¼èªå¨è§£ææfade-leave-active,fade-enter-acitive.... -->
      <transition-group name="fade">
        <div class="slider-item"
        v-show="idx===currentIdx"
        v-for="(item,idx) in list"
        :key="item.url">
          <img :src="item.url" :alt="item.alt" />
        </div>
      </transition-group>

    </div>

    <!-- å·¦å³æé® -->
    <span class="btn btn_left" @click="hPrev"></span>
    <span class="btn btn_right" @click="hNext"></span>

    <!-- æ é¢åºå -->
    <div class="txt">{{this.list[currentIdx].alt}}</div>
    <!-- æç¤ºæ¡ -->
    <ol class="indirector">
      <!-- // åªæ¯idxæ¯å½åè¦æ¾ç¤ºçå¾ï¼æä¼æ·»å current -->
      <!-- @mouseenter="   currentIdx=idx   " -->
      <li
      v-for="(item,idx) in list"
      :key="idx"
      @mouseenter="hDirectorEnter(idx)"
      :class="{current:idx===currentIdx}">
      </li>

      <!-- <li class="current"></li> -->
    </ol>
  </div>
</template>
<script>
export default {
  name: 'MySlider',
  props: {
    // è½®æ­åè¡¨
    list: {
      type: Array,
      required: true
    },
    // å½åå¼å§æ­æ¾æ¯åªä¸å¼ å¾
    curIdx: {
      type: Number,
      default: 0,
      required: false
    },
    // èªå¨æ­æ¾çæ¯«ç§æ°
    auto: {
      type: Number,
      default: 0, // é»è®¤ä¸å¼å¯èªå¨æ­æ¾
      required: false
    }
  },
  data () {
    return {
      // èªå®ä¹æ°æ®é¡¹æ¥æ¥æ¶curIdxå±æ§ã
      // å ä¸ºå¨ç»ä»¶åé¨ï¼ä¸åè®¸æ¹propså¼ç
      currentIdx: this.curIdx,
      timer: null
    }
  },
  created () {
    // å¦æç¨æ·è®¾ç½®autoï¼åå¼å¯å®æ¶æ­æ¾åè½
    this.move()
  },
  methods: {
    hClick () {
      // æç¹å»äºä»¶æç»ç¶çº§ç»ä»¶ï¼åå«ç¶ç»ä»¶æ¯åªå¼ å¾è¢«ç¹å»äº
      this.$emit('click', this.currentIdx)
    },
    hDirectorEnter (idx) {
      // console.log('é¼ æ è¿å¥çæç¤ºæ¡', idx)
      // è®¾ç½®å®ä¸º å½åå¾çç´¢å¼
      this.currentIdx = idx
    },
    move () {
      if (this.auto) {
      // æ¯éthis.autoæ¯«ç§ï¼å°±å»æ§è¡ä¸æ¬¡ï¼æ­æ¾ä¸ä¸å¼ 
      // è®°å½å®æ¶å¨ç¼å·
        this.timer = setInterval(() => {
          this.hNext()
        }, this.auto)
      }
    },
    hMouseenter () {
      // å¦ææå®æ¶å¨å¨å·¥ä½ï¼åè¦æ¸æ¥å®æ¶å¨ï¼è¿æ ·æè½è®©å¨ç»åä¸æ¥
      // clearInterval(å®æ¶å¨ç¼å·)
      // console.log('hMouseenter, å é¤å®æ¶å¨')
      clearInterval(this.timer)
    },
    hMouseleave () {
      // console.log('hMouseleave, å¼å¯å®æ¶å¨')

      // éå¯å®æ¶å¨ï¼å¼å§èªå¨è½®æ­
      this.move()
    },
    hNext () {
      // åæ¢ä¸ä¸å¼ 
      // æ¬è´¨å°±æ¯ä¿®æ¹  å½åæ¾ç¤ºå¾ç çç´¢å¼å¼ï¼ä¸æ ï¼
      // console.log(this.currentIdx)
      this.currentIdx = this.currentIdx + 1

      // é²æ­¢è¶ç
      if (this.currentIdx === this.list.length) {
        this.currentIdx = 0
      }
      // console.log(this.currentIdx)
    },
    hPrev () {
      // åæ¢ä¸ä¸å¼ 
      this.currentIdx = this.currentIdx - 1
      // é²æ­¢è¶ç
      if (this.currentIdx === -1) {
        this.currentIdx = this.list.length - 1
      }
    }
  },
  watch: {
    currentIdx () {
      // console.log('è½®æ­ååäº ', this.currentIdx)
      this.$emit('sliderChange', this.currentIdx)
    }
  }
}
</script>
<style>
.fade-enter-active,.fade-leave-active {
  /* // ææçè¿æ¸¡é½å¨0.5såå®æ */
  transition: all 0.5s ;
}
.fade-enter, .fade-leave-to{
  /* transform: translateX(400px); */
  opacity: 0;
}

.slider .slider-content,
.slider img {
  cursor:pointer;
  width: 100%;
  height: 100%;
}
.slider {
  margin: 0 auto;
  border: 1px solid #ccc;
  position: relative;
}
.slider .slider-content {
  overflow: hidden;
  position: relative;
}
.slider .slider-content .slider-item {
  position: absolute;
  top: 0;
  left: 0;
}
.slider img {
  width: 100%;
}
.slider .btn,
.slider .txt,
.slider .indirector {
  position: absolute;
}
.slider .btn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  top: 50%;
  transform: translateY(-50%);
  transition: background-color 0.2s;
}
.slider .btn:hover {
  background-color: rgba(255, 255, 255, 1);
}

.slider .btn:before,
.slider .btn:after {
  content: "";
  height: 3px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  left: 15px;
  top: 23px;
  transform: rotateZ(60deg);
  transform-origin: 0px center;
  transition: all 0.2s;
}
.slider .btn:after {
  transform: rotateZ(-60deg);
}
.slider .btn:hover:before {
  transform: rotateZ(45deg);
  background-color: red;
}
.slider .btn:hover:after {
  transform: rotateZ(-45deg);
  background-color: red;
}

.slider .btn.btn_right:before,
.slider .btn.btn_right:after {
  transform-origin: right center;
}

.slider .btn.btn_left {
  left: 20px;
}
.slider .btn.btn_right {
  right: 20px;
}

.slider .txt {
  text-indent: 1em;
  line-height: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: left;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #fff;
}
.slider .indirector {
  bottom: 10px;
  right: 1em;
}
.slider .indirector li {
  display: inline-block;
  margin: 0 5px;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #fff;
}
.slider .indirector li {
  transition: transform 0.2s;
}
.slider .indirector .current {
  background-color: #369;
  transform: scale(1.2);
}
</style>
