  props: {
    // éè¿ç¶ç»ä»¶ä¸­v-modelä¼ å¥
    value: {
      type: Boolean,
      required: false,
      default: false
    },
    title: {
      type: String,
      default: 'ç³»ç»æç¤º',
      require: false
    },
    content: {
      type: String,
      default: 'æ¶é´ä¸å¤äº',
      require: false
    },
    noTxt: {
      type: String,
      default: 'åæ¶',
